<template>
  <div class="app-container">
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-row>
          <el-form-item label=" : " prop="storageId">
            <el-link
              type="primary"
              style="min-width: 178px"
              @click="chooseLocation"
            >
              {{ query.storageId ? query.currentPosition : "" }}
            </el-link>
          </el-form-item>
          <el-button v-if="query.storageId" type="danger" @click="clearLocation"></el-button>
        </el-row>
        <el-row>
          <el-form-item prop="materialCodeOrName" label=" : ">
            <el-input
              v-model.trim="query.materialCodeOrName"
              clearable
              placeholder=""
              class="filter-item"
              style="width: 178px;"
              @keyup.enter.native="searchInit"
            />
          </el-form-item>
          <el-form-item prop="materialGroupCode">
            <el-select
              v-model="query.materialGroupCode"
              filterable
              remote
              placeholder=""
              :remote-method="remoteMethod"
              :loading="selectLoading"
              class="filter-item"
              style="width: 178px;"
              clearable
              @clear="clearInfo"
            >
              <el-option
                v-for="item in options"
                :key="item.code"
                :label="item.code"
                :value="item.code"
              />
            </el-select>
          </el-form-item>
          <el-form-item prop="materialType">
            <el-select
              v-model="query.materialType"
              class="filter-item"
              placeholder=""
              filterable
              clearable
              style="width: 178px;"
            >
              <el-option v-for="(item, index) in dict['wms_material_type']" :key="index" :label="item.label" :value="item.value" />
            </el-select>
          </el-form-item>
          <el-form-item prop="assessType">
            <el-select
              v-model="query.assessType"
              class="filter-item"
              placeholder=""
              filterable
              clearable
              style="width: 178px;"
            >
              <el-option v-for="(item, index) in dict['wms_material_assess_type']" :key="index" :label="item.label" :value="item.value" />
            </el-select>
          </el-form-item>
          <el-form-item prop="batchNoOrBarcode">
            <el-input
              v-model.trim="query.batchNoOrBarcode"
              clearable
              placeholder="SN"
              class="filter-item"
              style="width: 178px;"
            />
          </el-form-item>
          <!-- <el-form-item prop="expirationDate">
            <el-input-number
              v-model="query.expirationDate"
              :min="0"
              placeholder=""
              style="width: 178px;"
            />
          </el-form-item> -->
          <el-form-item prop="materialImportantGrade">
            <el-select
              v-model="query.materialImportantGrade"
              class="filter-item"
              placeholder=""
              filterable
              clearable
              style="width: 178px;"
            >
              <el-option v-for="(item, index) in dict['wms_material_important_grade']" :key="index" :label="item.label" :value="item.value" />
            </el-select>
          </el-form-item>
          <el-form-item prop="earlyWarningType">
            <el-select
              v-model="query.earlyWarningType"
              class="filter-item"
              placeholder=""
              filterable
              clearable
              style="width: 178px;"
            >
              <el-option label="" :value="1" />
              <el-option label="" :value="2" />
              <el-option label="" :value="3" />
            </el-select>
          </el-form-item>
        </el-row>
        <!-- <el-row>
          <el-form-item label=": " prop="startTime">
            <el-input-number v-model="query.num" :min="1" :max="10" label="" />
          </el-form-item>
        </el-row> -->
        <el-row>
          <el-form-item label=" : " prop="startTime">
            <el-date-picker
              v-model="query.startTime"
              class="filter-item"
              type="date"
              style="width: 178px;"
              value-format="yyyy-MM-dd"
              placeholder=""
              @change="query.endTime = ''"
            />
          </el-form-item>
          <el-form-item prop="endTime">
            <el-date-picker
              v-model="query.endTime"
              class="filter-item"
              type="date"
              style="width: 178px;"
              value-format="yyyy-MM-dd"
              placeholder=""
              :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startTime).getTime()}"
            />
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="searchInit"
          ></el-button>
          <el-button
            class="filter-item"
            type="warning"
            icon="el-icon-refresh-left"
            @click="resetForm('filterItem')"
          ></el-button>
        </el-row>
      </el-form>
    </div>
    <el-descriptions :column="2">
      <el-descriptions-item label="">{{ totalAmount || '---' }}</el-descriptions-item>
    </el-descriptions>
    <el-row style="text-align: right;">
      <el-button type="success" icon="el-icon-download" @click="downloadTemplate"></el-button>
      <template v-if="!sortInfo">
        <el-button type="primary" @click="groupingStatistics"></el-button>
        <el-button type="primary" icon="el-icon-download" @click="exportAccount"></el-button>
      </template>
      <template v-else>
        <el-button type="primary" @click="cancelGroupingStatistics"></el-button>
        <el-button type="primary" icon="el-icon-download" @click="exportList"></el-button>
      </template>
      <!-- /  -->
      <form-upload-file
        ref="uploadCom"
        accept=".xls,.xlsx"
        style="margin-left: 10px;"
        url="/warehouseMgt/wmsInventoryBookDetail/import"
        btn-content=""
        btn-icon="el-icon-upload"
        :limit="99999"
        :show-file-list="false"
        @uploadFileSuccess="handleUploadSuccess"
        @uploadFileError="handleUploadFileError"
      >
        <template v-slot:tip />
      </form-upload-file>
    </el-row>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :cell-style="handleCellStyle"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @refresh="refresh"
      >
        <template slot-scope="scope">
          <el-button v-if="!scope.row.isSubtotalsRow && !scope.row.isTotalsRow" type="warning" @click="toDetail(scope.row)"></el-button>
        </template>
      </table-com>
    </el-row>
    <pagination v-if="isPage" :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <storage-location v-if="kuweiVisible" v-model="kuweiVisible" @confirm="selectedStorageLocation" />
    <sort v-model="visible" :extra-info="{columnList}" @confirm="handleConfirm" />
    <order-info v-model="visible1" :extra-info="rowInfo" />
    <reservation-info v-model="visible2" :extra-info="rowInfo" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import paginationPage from '@/mixins/paginationPage'
import { getListApi, groupExportApi, groupListApi, accountExportApi } from '@/apis/modules/warehouse/inventory'
import { getPullListItemApi } from '@/apis/modules/base/baseItemGroup'
import StorageLocation from './StorageLocation'
import Sort from './Sort'
import OrderInfo from './OrderInfo'
import ReservationInfo from './ReservationInfo'
import { requestWrapper } from '@/utils/utils'
import tableHeadSet from '@/mixins/tableHeadSet'
import { formUploadFilesMixins } from '@/mixins/upload'

export default {
  name: 'WarehouseInventory',
  components: {
    StorageLocation,
    Sort,
    OrderInfo,
    ReservationInfo
  },
  mixins: [paginationPage, tableHeadSet, formUploadFilesMixins],
  props: {
    // params
    materialCode: {
      type: [String, Number],
      default: ''
    }
  },
  dicts: ['wms_material_assess_type', 'wms_material_type', 'wms_material_important_grade'],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        storageId: '', // id
        currentPosition: '', // 
        materialGroupCode: '', // 
        materialType: '', // 
        assessType: '', // 
        materialCodeOrName: '', // 
        batchNoOrBarcode: '', // SN
        num: '',
        // expirationDate: '', // 
        earlyWarningType: '', // 
        materialImportantGrade: '', // 
        startTime: '', // 
        endTime: '' // 
      },
      tableData: [],
      totalAmount: '', // ()
      columnSets: [
        {
          prop: 'outWarehouse',
          children: [
            {
              module: 'word',
              prop: 'outWarehouseNum',
              label: '',
              align: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: null,
              headerAlign: 'center'
            },
            {
              module: 'word',
              prop: 'outWarehouseAmount',
              label: '',
              align: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: null,
              headerAlign: 'center'
            }
          ]
        },
        {
          prop: 'inWarehouse',
          children: [
            {
              module: 'word',
              prop: 'outWarehouseNum',
              label: '',
              align: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: null,
              headerAlign: 'center'
            },
            {
              module: 'word',
              prop: 'outWarehouseAmount',
              label: '',
              align: 'center',
              minWidth: '',
              fixed: false,
              sortable: false,
              resizable: true,
              formatter: null,
              headerAlign: 'center'
            }
          ]
        },
        {
          module: 'word',
          prop: 'earlyWarningInfo',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          vHtml: true,
          formatter: item => `${item.earlyWarningInfo.split(',').map(a => `${a}<br />`).join('')}`,
          headerAlign: 'center'
        },
        // 
        {
          module: 'link',
          prop: 'inTransitNum',
          formatter: (row) => row.inTransitNum,
          handleClick: (c) => {
            console.log('', c)
            if (!c.inTransitNum) return
            this.visible1 = true
            this.rowInfo = { ...c }
          }
        },
        // 
        {
          module: 'link',
          prop: 'billReservedNum',
          formatter: (row) => row.billReservedNum,
          handleClick: (c) => {
            console.log('', c)
            if (!c.billReservedNum) return
            this.visible2 = true
            this.rowInfo = { ...c }
          }
        }
      ],
      options: [],
      selectLoading: false,
      kuweiVisible: false,
      isPage: true, // 
      visible: false,
      sortInfo: null,
      visible1: false,
      visible2: false,
      rowInfo: {}
    }
  },
  computed: {
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.materialCode && (this.query.materialCodeOrName = this.materialCode)
    this.init()
  },
  methods: {
    init(type, sortParams = {}) {
      this.loading = true
      type === 'sort' ? this.isPage = false : this.isPage = true
      const params = {
        ...type !== 'sort' && { page: {
          ...this.pageInfo
        }},
        ...type === 'sort' && sortParams,
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      const api = type === 'sort' ? groupListApi : getListApi
      return new Promise((resolve, reject) => {
        api({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData, otherData, pageList } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            this.totalAmount = otherData.totalAmount
            if (type !== 'sort') {
              const { list, ...pageInfo } = pageData
              this.initPage(pageInfo)
              this.tableData = list
            } else {
              this.tableData = pageList
            }
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.query.currentPosition = ''
      this.searchInit()
    },
    toDetail({ materialId }) {
      this.$router.push({
        name: 'WarehouseInventoryDetail',
        params: {
          materialId
        }
      })
    },
    chooseLocation() {
      // this.$router.push({
      //   name: 'WarehouseChooseWarehouseLocation'
      // })
      this.kuweiVisible = true
    },
    clearLocation() {
      this.query.currentPosition = ''
      this.query.storageId = ''
    },
    selectedStorageLocation({ currentPosition, storageId }) {
      this.query.currentPosition = currentPosition
      this.query.storageId = storageId
    },
    // 
    // 
    async remoteMethod(query) {
      this.options = []
      if (query !== '') {
        this.selectLoading = true
        // Tip 
        const res = await requestWrapper(getPullListItemApi({
          materialGroupCode: query
        }))
        if (!res.success) return
        this.selectLoading = false
        this.options = res.data
      } else {
        this.options = []
      }
    },
    clearInfo() {
      // console.log()
      this.options = []
    },
    // Tip 
    handleCellStyle({ row, rowIndex }) {
      let cellStyle = {}
      // 
      if (row.earlyWarningInfo) {
        cellStyle = {
          backgroundColor: '#F56C6C'
        }
      } else {
        // isSubtotalsRow 
        // isTotalsRow 
        row.isSubtotalsRow && (cellStyle = {
          backgroundColor: '#FDFEDF',
          fontWeight: 750
        })
        row.isTotalsRow && (cellStyle = {
          backgroundColor: '#FEFDBF',
          fontWeight: 900
        })
      }
      return cellStyle
    },
    // 
    handleConfirm(params) {
      this.sortInfo = params
      this.init('sort', params)
    },
    // 
    groupingStatistics() {
      this.visible = true
    },
    // 
    cancelGroupingStatistics() {
      this.sortInfo = null
      this.init()
    },
    // 
    exportAccount() {
      // this.$notify.warning({
      //   title: '',
      //   message: ''
      // })
      const params = {
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      this.$confirm('?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          accountExportApi(params)
            .then(res => {
              this.download(res, '.xlsx')
            })
            .catch(e => {
              console.log(e)
              this.$notify.error({
                title: '',
                message: ''
              })
            })
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    // Tip 
    exportList() {
      const params = {
        ...this.sortInfo,
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      this.$confirm('?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          groupExportApi(params)
            .then(res => {
              this.download(res, '.xlsx')
            })
            .catch(e => {
              console.log(e)
              this.$notify.error({
                title: '',
                message: ''
              })
            })
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    download(result, name) {
      const filename = name
      const blob = new Blob([result.data], { type: 'application/vnd.ms-excel' })
      const url = window.URL.createObjectURL(blob)
      if (window.navigator.msSaveBlob) { // IE
        try {
          window.navigator.msSaveBlob(blob, filename)
        } catch (e) {
          console.log(e)
        }
      } else { // IE
        const link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', filename)
        document.body.appendChild(link)
        link.click()
      }
      URL.revokeObjectURL(url) // 
    },
    refresh() {
      this.sortInfo ? this.init('sort', this.sortInfo) : this.searchInit()
    },
    // 
    handleUploadSuccess() {
      this.init()
      this.$notify.success({
        title: '',
        message: ''
      })
    },
    // 
    handleUploadFileError() {
      this.$notify.error({
        title: '',
        message: ''
      })
    },
    // 
    downloadTemplate() {
      window.open(`${process.env.VUE_APP_BASE_API_WMS}/warehouseMgt/wmsInventoryBookDetail/downloadBookImportTemplate`)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-form .el-row{
  margin-bottom: 10px;
}
</style>
